<template>
  <div class="fillcontain">
    <h3>人员管理</h3>
    <div class="contain">
      <div class="table_container">
        <el-row class="readWrapper">
          <el-col :span="7">
            <span class="userSpan">按姓名：</span>
            <el-input class="inputRead" placeholder="请输入姓名" v-model="nameSearch"></el-input>
          </el-col>
          <el-col :span="2">
            <el-button type="primary" @click="search" class="userSearch" icon="el-icon-search">搜索</el-button>
          </el-col>
          <el-button type="primary" class="userAdd" v-show="addShow" @click="addUser">新增</el-button>
        </el-row>
        <el-table
          v-loading="loading"
          :data="tableData"
          border
          stripe
          highlight-current-row
          header-cell-class-name="table-header-class"
          style="width:100%"
        >
          <el-table-column label="序号" width="60" align="center">
            <template slot-scope="scope">
              <span>{{scope.$index+(paginations.pageIndex-1) * paginations.pageSize + 1}}</span>
            </template>
          </el-table-column>
          <el-table-column property="name" label="姓名" align="center"></el-table-column>
          <el-table-column property="planename" label="代字" align="center"></el-table-column>
          <el-table-column property="power_y" label="体能" align="center" :formatter="formatTime3"></el-table-column>
          <el-table-column property="site" label="舱位" :formatter="formatSite" align="center"></el-table-column>
          <el-table-column property="state" label="人员状态" :formatter="formatState" align="center"></el-table-column>
          <el-table-column property="phone" label="电话" align="center"></el-table-column>
          <el-table-column property="birthday" label="出生日期" :formatter="formatTime2" align="center"></el-table-column>
          <el-table-column property="time" label="创建时间" :formatter="formatTime" align="center"></el-table-column>
          <el-table-column fixed="right" label="操作" width="250">
            <template slot-scope="scope">
              <i @click="editClick(scope.row)" class="el-icon-edit userBut" slot="reference"></i>
              <el-popconfirm v-show="addShow" title="确定删除吗？" @onConfirm="deleteOk(scope.row,0)">
                <i class="el-icon-delete userBut" slot="reference"></i>
              </el-popconfirm>
              <el-popconfirm v-show="addShow" title="确定冻结人员吗？" @onConfirm="deleteOk(scope.row,2)">
                <i class="el-icon-s-release userBut" slot="reference"></i>
              </el-popconfirm>
              <el-popconfirm v-show="addShow" title="确定解冻结人员吗？" @onConfirm="deleteOk(scope.row,1)">
                <i class="el-icon-finished userBut" slot="reference"></i>
              </el-popconfirm>
              <el-popconfirm v-show="addShow" title="确定重置密吗？" @onConfirm="pwdClick(scope.row)">
                <i class="el-icon-s-open userBut" slot="reference"></i>
              </el-popconfirm>
            </template>
          </el-table-column>
        </el-table>
        <el-row>
          <el-col :span="24">
            <div class="pagination">
              <el-pagination
                v-if="paginations.total > 0"
                :page-sizes="paginations.pageSizes"
                :page-size="paginations.pageSize"
                :layout="paginations.layout"
                :total="paginations.total"
                :current-page="paginations.pageIndex"
                @current-change="handleCurrentChange"
                @size-change="handleSizeChange"
              ></el-pagination>
            </div>
          </el-col>
        </el-row>
      </div>
    </div>
    <el-dialog
      title
      @click="QXbut"
      id="customWidth"
      :visible.sync="dialogFormVisible"
      class="customWidth"
    >
      <el-form :model="form" :rules="rules">
        <div class="iconUser"></div>
        <img :src="form.icon" id="img" alt />
        <input type="file" name="aatraj" id="aatraj2" @change="uploadImg" />
        <el-form-item label="名字：" :label-width="formLabelWidth">
          <el-input v-model="form.name" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="代字：" :label-width="formLabelWidth">
          <el-input v-model="form.planename" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="体能：" prop="power_y" :label-width="formLabelWidth">
          <el-input v-model="form.power_y" autocomplete="off"></el-input>
        </el-form-item>
        <el-form-item label="密码：" :label-width="formLabelWidth">
          <!-- <el-input type="password" v-model="form.passwd" autocomplete="off"></el-input> -->
          <el-input type="password" v-model="form.passwd"></el-input>
        </el-form-item>
        <el-form-item label="舱位：" :label-width="formLabelWidth">
          <el-select class="csrq" v-model="form.site" placeholder="请选择">
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="角色：" :label-width="formLabelWidth">
          <el-select class="csrq" v-model="form.mode" placeholder="请选择">
            <el-option
              v-for="item in optionsJS"
              :key="item.value"
              :label="item.label"
              :value="item.value"
            ></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="出生日期：" :label-width="formLabelWidth">
          <el-date-picker class="csrq" v-model="form.birthday" type="date" placeholder="选择日期"></el-date-picker>
        </el-form-item>
        <el-form-item label="联系电话：" prop="phone" :label-width="formLabelWidth">
          <el-input v-model="form.phone" autocomplete="off"></el-input>
        </el-form-item>
      </el-form>
      <div>
        <el-button class="userButModle" @click="QXbut">取 消</el-button>
        <el-button class="userButModle" type="primary" @click="add">确 定</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { getUserList, add, edit } from "@/api/user";
import { getToken } from "@/utils/auth";
import md5 from "js-md5";
import axios from "axios";
import { debuglog } from "util";
export default {
  data() {
    // 手机号验证
    var checkPhone = (rule, value, callback) => {
      const phoneReg = /^1[3|4|5|6|7|8][0-9]{9}$/;
      if (!value) {
        return callback(new Error("电话号码不能为空"));
      }
      setTimeout(() => {
        if (!Number.isInteger(+value)) {
          callback(new Error("请输入数字值"));
        } else {
        }
      }, 100);
    };
    var power_yYZ = (rule, value, callback) => {
      if (value > 100 || value < 0) {
        return callback(new Error("体能0-100之间"));
      }
    };
    return {
      dialogImageUrl: "",
      rules: {
        phone: [{ required: true, validator: checkPhone, trigger: "blur" }],
        power_y: [{ required: true, validator: power_yYZ, trigger: "blur" }]
      },
      src: "",
      addShow: true,
      options: [
        {
          value: "1",
          label: "前"
        },
        {
          value: "0",
          label: "后"
        },
        {
          value: "2",
          label: "全"
        }
      ],
      optionsJS: [
        {
          value: "1",
          label: "管理员"
        },
        {
          value: "0",
          label: "用户"
        }
      ],
      value: "",
      dialogVisible: false,
      form: {
        name: "",
        planename: "",
        power_y: "",
        passwd: "",
        site: "",
        mode: "",
        birthday: "",
        phone: ""
      },
      dialogFormVisible: false,
      formLabelWidth: "120px",
      nameSearch: "",
      valueDate: "",
      tableData: [],
      loading: false,
      addType: 1,
      paginations: {
        total: 0,
        pageIndex: 1,
        pageSize: 10,
        pageSizes: [10, 20, 30, 40],
        layout: "total, sizes, prev, pager, next, jumper"
      }
    };
  },
  created() {},
  mounted() {
    this.init();
    this.getUserList({
      count: this.paginations.pageSize,
      index: this.paginations.pageIndex,
      search: {
        state: {
          $gt: 0
        }
      }
    });
  },
  methods: {
    uploadImg() {
      var file = document.getElementById("aatraj2").files[0];
      if (window.FileReader) {
        var reader = new FileReader();
        reader.readAsDataURL(file);
        //监听文件读取结束后事件
        reader.onloadend = e => {
          this.form.icon = e.target.result;
        };
      }
    },
    init() {
      var user = this.sessionData("get", "user");
      if (user && user.mode == 1) {
        this.addShow = true;
      } else {
        this.addShow = false;
      }
    },
    addUser() {
      for (var i in this.form) {
        this.form[i] = "";
      }
      this.addType = 1;
      this.dialogFormVisible = true;
    },
    formatTime(row) {
      return this.$tool.formatDate(new Date(row.time));
    },

    formatTime2(row) {
      if (row.birthday) {
        return this.$tool.formatDate(new Date(row.birthday));
      }
    },

    formatTime3(row) {
      if (row.power_y) {
        return Number(row.power_y).toFixed(0);
      }
    },

    QXbut() {
      this.getUserList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        search: {
          state: {
            $gt: 0
          }
        }
      });
      this.dialogFormVisible = false;
    },

    pwdClick(row) {
      var passwd = "123456";
      var del = {
        _id: row._id,
        data: {
          passwd: md5(passwd)
        }
      };
      edit(del).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "重置密码成功！",
            type: "success"
          });
        }
      });
    },

    deleteOk(row, type) {
      var del = {
        _id: row._id,
        data: {
          state: type
        }
      };
      edit(del).then(res => {
        if (res.code == 0) {
          this.$message({
            message: "人员删除成功！",
            type: "success"
          });
          this.getUserList({
            count: this.paginations.pageSize,
            index: this.paginations.pageIndex,
            search: {
              state: {
                $gt: 0
              }
            }
          });
        }
      });
    },

    formatSite(row) {
      return row.site == 1 ? "前" : row.site == 2 ? "全" : "后";
    },

    formatState(row) {
      return row.state == 1 ? "正常" : row.state == 2 ? "冻结" : "";
    },

    getUserList(obj) {
      var data = this.$tool.deep(obj);
      data.index -= 1;
      getUserList(data).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          var user = this.sessionData("get", "user");
          if (user && user.mode == 1) {
            this.loading = false;
            this.paginations.total = list.total;
            this.tableData = list.array;
            var userArr = this.$tool.deep(list.array);
            var arr = [];
            for (var i in userArr) {
              delete userArr[i].icon;
              if (userArr[i].state == 1) {
                arr.push(userArr[i]);
              }
            }
            this.sessionData("set", "userList", arr);
          } else {
            for (var i = 0; i < list.array.length; i++) {
              if (user._id == list.array[i]._id) {
                this.tableData = [list.array[i]];
              }
            }
          }
        }
        this.getUserArr();
      });
    },

    getUserArr() {
      var data = {
        count: 1000,
        index: 0,
        search: { state: { $gt: 0 } }
      };
      getUserList(data).then(res => {
        if (res.code == 0) {
          var list = this.$unfly(res.data);
          for (var i in list.array) {
            delete list.array[i].icon;
          }
          this.sessionData("set", "userListAll", list.array);
          var user = this.sessionData("get", "user");
          if (user && user.mode == 1) {
            var userArr = this.$tool.deep(list.array);
            var arr = [];
            for (var i in userArr) {
              if (userArr[i].state == 1) {
                arr.push(userArr[i]);
              }
            }
            this.sessionData("set", "userList", arr);
          } else {
            for (var i = 0; i < list.array.length; i++) {
              if (user._id == list.array[i]._id) {
                var arr = [list.array[i]];
                this.sessionData("set", "userList", arr);
              }
            }
          }
        }
      });
    },

    search() {
      var data = {
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        search: {
          state: {
            $gt: 0
          }
        }
      };
      if (!this.valueDate) delete data.search.time;
      this.getUserList(data);
    },

    editClick(row) {
      var row = this.$tool.deep(row);
      row.mode = row.mode == undefined ? "" : row.mode + "";
      row.site = row.site == undefined ? "" : row.site + "";
      this.form = row;
      this.addType = 2;
      this.dialogFormVisible = true;
    },

    editFun() {
      var form = this.$tool.deep(this.form);
      form.mode = Number(form.mode);
      form.site = Number(form.site);
      try {
        form.birthday = form.birthday.getTime();
      } catch (error) {}
      form.passwd = md5(form.passwd);
      form.state = 1;
      delete form.name;
      delete form.passwd;
      delete form.planename;
      delete form._id;
      var formData = new FormData();
      formData.append("data", JSON.stringify(form));
      formData.append("token", getToken("Token"));
      formData.append("_id", this.form._id);
      this.addUserWay(formData, 1);
    },

    add() {
      if (this.addType == 1) {
        this.addFun();
      } else {
        this.editFun();
      }
    },

    addFun() {
      if (
        !this.form.name ||
        !this.form.planename ||
        !this.form.power_y ||
        !this.form.passwd ||
        !this.form.site ||
        !this.form.mode ||
        !this.form.birthday ||
        !this.form.phone
      ) {
        this.$message({
          message: "每一项都是必填项请将信息填写完整！",
          type: "warning"
        });
        return;
      }

      this.form.mode = Number(this.form.mode);
      this.form.site = Number(this.form.site);
      try {
        this.form.birthday = this.form.birthday.getTime();
      } catch (error) {}
      this.form.passwd = md5(this.form.passwd);
      this.form.state = 1;
      var user = { data: this.form };
      this.form.token = getToken("Token");

      var formData = new FormData();
      formData.append("data", JSON.stringify(this.form));
      formData.append("token", getToken("Token"));
      this.addUserWay(formData);
    },

    addUserWay(formData, type) {
      var url =
        window.baseURL ||
        sessionStorage.getItem("serverUrl_str") ||
        "http://127.0.0.1:8088";
      axios
        .post(url + (type ? "/user/modify" : "/user/add"), formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(res => {
          if (res.data.code == 0) {
            this.$message({
              message: this.form._id ? "修改人员信息成功！" : "添加人员成功！",
              type: "success"
            });
            this.dialogFormVisible = false;
            this.getUserList({
              count: this.paginations.pageSize,
              index: this.paginations.pageIndex,
              search: {
                state: {
                  $gt: 0
                }
              }
            });

            for (var i in this.form) {
              this.form[i] = "";
            }
          }
        });
    },

    addUserWay1(user) {
      add(user).then(res => {
        if (res.code == 0) {
          this.$message({
            message: user._id ? "修改人员信息成功！" : "添加人员成功！",
            type: "success"
          });
          this.dialogFormVisible = false;
          this.getUserList({
            count: this.paginations.pageSize,
            index: this.paginations.pageIndex,
            search: {
              state: {
                $gt: 0
              }
            }
          });

          for (var i in this.form) {
            this.form[i] = "";
          }
        }
      });
    },

    // 每页多少条切换
    handleSizeChange(pageSize) {
      this.paginations.pageSize = pageSize;
      this.getUserList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        search: {
          state: {
            $gt: 0
          }
        }
      });
    },

    // 上下分页
    handleCurrentChange(page) {
      this.paginations.pageIndex = page;
      this.getUserList({
        count: this.paginations.pageSize,
        index: this.paginations.pageIndex,
        search: {
          state: {
            $gt: 0
          }
        }
      });
    }
  }
};
</script>
